import {Component, Input, Output, EventEmitter, OnInit} from "@angular/core";
import {ApiService} from "../../services/api.service";
import {Router, ActivatedRoute} from "@angular/router";
import {ToastsManager} from "ng2-toastr";
import {Response} from "@angular/http";

@Component({
    selector: '[event-item]',
    templateUrl: './event-item.component.html'
})
export class EventItemComponent implements OnInit {
    @Input() item: any;
    @Output() onRemoval: EventEmitter<any>;

    private opened: boolean = false;

    constructor(private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {

        this.onRemoval = new EventEmitter();
    }

    ngOnInit(): void {
        this.prepareDates();
    }

    private prepareDates() {

    }

    editEvent(event: any, anEvent: any) {
        event.preventDefault();
        this.router.navigate(['./', anEvent.id], {relativeTo: this.activatedRoute});
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    deleteEvent(id: number) {
        this.apiService
            .delete("/events/".concat(this.item.event.id))
            .subscribe(
                (response: Response) => {
                    this.onRemoval.emit(this.item.event);
                    this.toggleDialog();
                    this.toastsManager.success(this.item.event.title.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to delete ".concat(this.item.event.title), "Failure");
                    this.toggleDialog();
                }
            );
    }

    toggleDialog() {
        this.opened = !this.opened;
    }
}



