import {Component, OnInit, Input, EventEmitter, Output, Inject} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {ApiService} from "../../services/api.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
  selector: '[region-item]',
  templateUrl: './region-item.component.html'
})
export class RegionItemComponent {

    @Input() region: any;
    @Output() onRemoval: EventEmitter<any>;

    private opened: boolean = false;

    constructor(@Inject("apiUrl") private apiUrl: string,
                private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {

        this.onRemoval = new EventEmitter();
    }

    editRegion(event: any, region: any) {
        event.preventDefault();
        this.router.navigate(['./', region.id], {relativeTo: this.activatedRoute});
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    toggleDialog() {
        this.opened = !this.opened;
    }

    deleteRegion() {
        this.apiService
            .delete("/regions/".concat(this.region.id))
            .subscribe(
                (response: Response) => {
                    this.onRemoval.emit(this.region);
                    this.toggleDialog();
                    this.toastsManager.success(this.region.name.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to delete ".concat(this.region.name), "Failure");
                }
            );
    }

}



