import {Component} from "@angular/core";

@Component({
    selector: "events",
    template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class EventsComponent {
    private pageHeader: string;

    constructor() {
        this.pageHeader = "Events";
    }
}



