import { Component, OnInit } from '@angular/core';
import {Region} from "../region";
import {ActivatedRoute, Router} from "@angular/router";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {Response} from "@angular/http";

@Component({
    selector: 'app-region-form',
    templateUrl: './region-form.component.html'
})
export class RegionFormComponent implements OnInit {
    private id: number;
    private region: any;
    private regionForm: FormGroup;
    private pageLoadingComplete: boolean = false;

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager) {

        this.activatedRoute.params.subscribe((params: any) => this.id = params['id']);

        this.region = {
            name: undefined,
            deleted: [false],
        };
    }

    ngOnInit() {
        if (this.newRegion()) {
            this.initForm();
        } else {
            this.loadRegion();
        }
    }

    loadRegion() {
        this.apiService
            .get("/regions/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.region = response.json();
                    this.initForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve user with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    private initForm() {
        this.regionForm = this.formBuilder.group({
            name: [this.region.name, Validators.required]
        });
        this.pageLoadingComplete = true;
    }

    private newRegion() {
        return isNaN(this.id) ? true : false;
    }

    onSubmit() {
        if (this.newRegion()) {
            this.saveRegion();
        } else {
            this.updateRegion();
        }
    }

    private saveRegion(): void {
        this.apiService
            .post("/regions", this.regionForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["regions"]);
                    this.toastsManager
                        .success("Region ".concat(this.regionForm.value.name).concat(" has been created successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to save ".concat(this.regionForm.value.name), "Failure")
                }
            );
    }

    private updateRegion(): void {
        this.apiService
            .put("/regions/".concat(this.id.toString()), this.regionForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["regions"]);
                    this.toastsManager
                        .success("Region ".concat(this.regionForm.value.name).concat(" has been updated successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update ".concat(this.regionForm.value.name), "Failure")
                }
            );
    }


}



