import {Component} from "@angular/core";

@Component({
  selector: 'app-regions',
  template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class RegionsComponent {
  private pageHeader: string;

  constructor() {
    this.pageHeader = "Regions";
  }
}



