import {Component, Inject, ViewContainerRef} from "@angular/core";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {Response} from "@angular/http";
import {Router} from "@angular/router";
import {ApiService} from "../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.css']
})
export class LoginComponent {
    private pageLoadingComplete: boolean = true;
    private loginForm: FormGroup;

    constructor(private router: Router,
                fb: FormBuilder,
                private apiService: ApiService,
                @Inject("currentUser") private currentUser: string,
                private toastsManager: ToastsManager,
                containerRef: ViewContainerRef) {

        this.loginForm = fb.group({
            username: ['', Validators.required],
            password: ['', Validators.required],
        });
    }

    onSubmit() {
        this.pageLoadingComplete = false;

        this.apiService
            .post("/authenticate/login", this.loginForm.value)
            .subscribe(
                (response: Response) => {
                    let user = response.json();
                    localStorage.setItem(this.currentUser, JSON.stringify(user));
                    this.router.navigate(["users"]);
                    this.pageLoadingComplete = true;
                    this.toastsManager.success("You're logged in as ".concat(user.username), "Success");
                },
                (err: any) => {
                    this.toastsManager.error("Failed to authorize ".concat(this.loginForm.value.username), "Failure");
                    this.pageLoadingComplete = true;
                }
            );
    }
}



