import {Component} from '@angular/core';

@Component({
  selector: 'categories',
  template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class CategoriesComponent {
    private pageHeader: string;

    constructor() {
        this.pageHeader = "Categories";
    }
}



