import {Component} from "@angular/core";

@Component({
  selector: 'organizations',
  template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class OrganizationsComponent {
    private pageHeader: string;

    constructor() {
        this.pageHeader = "Organizations";
    }
}



