import {Component, Input, Output, EventEmitter, Inject} from '@angular/core';
import {Response} from "@angular/http";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: '[category-item]',
    templateUrl: './category-item.component.html'
})
export class CategoryItemComponent {
    @Input() category: any;
    @Output() onRemoval: EventEmitter<any>;

    private opened: boolean = false;

    constructor(@Inject("apiUrl") private apiUrl: string,
                private apiService: ApiService,
                private toastsManager: ToastsManager) {

        this.onRemoval = new EventEmitter();
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    toggleDialog() {
        this.opened = !this.opened;
    }

    deleteCategory() {
        this.apiService
            .delete("/categories/".concat(this.category.id))
            .subscribe(
                (response: Response) => {
                    this.onRemoval.emit(this.category);
                    this.toggleDialog();
                    this.toastsManager.success(this.category.name.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to delete ".concat(this.category.name), "Failure");
                }
            );
    }

}



