import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiService, QueryParameter} from "../../services/api.service";
import {Response} from "@angular/http";
import {FormBuilder, FormGroup, Validators, AbstractControl} from "@angular/forms";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

function passwordValidator(control: AbstractControl): {[s: string]: boolean} {
    if (!control.parent) {
        return ;
    }

    let password = control.parent.controls["password"];
    let confirmedPassword = control.parent.controls["confirmedPassword"];

    if (password.dirty && confirmedPassword.dirty && password.value !== confirmedPassword.value) {
       return {passwordMismatch: true};
    }

    if (password.hasError("passwordMismatch")) {
       password.setErrors(null, {emitEvent: true});
    }
    if (confirmedPassword.hasError("passwordMismatch")) {
       confirmedPassword.setErrors(null, {emitEvent: true});
    }
}

@Component({
    selector: 'app-user-form',
    templateUrl: './user-form.component.html'
})
export class UserFormComponent implements OnInit {
    private emailRegex = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    pageLoadingComplete: boolean = false;

    userForm: FormGroup;

    private passwordInput: AbstractControl;
    private confirmedPasswordInput: AbstractControl;

    id: number;
    user: any = {};
    allRoles: Array<any>;
    additionalUsernameErrors: any;

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager) {

        this.allRoles = [
            {name: "ADMIN", authority: "ROLE_ADMIN", selected: false},
            {name: "USER", authority: "ROLE_USER", selected: false},
            {name: "GUEST", authority: "ROLE_GUEST", selected: false}
        ];

        this.additionalUsernameErrors = {
            invalidEmail: false,
            alreadyExists: false
        };
    }

    ngOnInit() {
        this.activatedRoute.params.subscribe((params: any) => {
            this.id = params['id'];

            if (this.newUser()) {
                this.initUser();
                this.initUserCreationFrom();
            } else {
                this.loadUser();
            }
        });
    }

    private newUser(): boolean {
        return Number.isNaN(Number.parseInt(this.id.toString()));
    }

    private loadUser(): any {
        this.apiService
            .get("/users/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.user = response.json();
                    this.initUserEditingForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve user with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    private initUser() {
        this.user = {
            username: null,
            password: null,
            confirmedPassword: null,
            enabled: true,
            roles: []
        };
    }

    private initUserCreationFrom() {
        this.userForm = this.formBuilder.group({
            username: [this.user.username, Validators.required],
            password: [null, Validators.compose([Validators.required, passwordValidator.bind(this)])],
            confirmedPassword: [null, Validators.compose([Validators.required, passwordValidator.bind(this)])],
            enabled: [this.user.enabled],
            roles: []
        });

        this.passwordInput = this.userForm.controls["password"];
        this.confirmedPasswordInput = this.userForm.controls["confirmedPassword"];
        this.pageLoadingComplete = true;
    }

    private initUserEditingForm() {
        this.userForm = this.formBuilder.group({
            username: [this.user.username, Validators.required],
            enabled: [this.user.enabled],
            roles: []
        });

        this.initRoles();
        this.pageLoadingComplete = true;
    }

    private initRoles() {
        this.allRoles.forEach((role: any) => {
            if (this.user.roles.find((userRole: any) => {return userRole.authority === role.authority})) {
                role.selected = true;
            }
        });
    }

    private onSubmit() {
        this.prepareRoles();
        if (this.newUser()) {
            this.saveUser();
        } else {
            this.updateUser();
        }
    }

    private prepareRoles() {
        this.user.roles = [];
        this.allRoles.forEach((role: any) => {
            if (role.selected) this.user.roles.push({authority: role.authority});
        });
        this.userForm.controls["roles"].setValue(this.user.roles);
    }

    private toggleRole(role: any) {
        role.selected = !role.selected;
    }

    private saveUser() {
        this.apiService
            .post("/users", this.userForm.value)
            .subscribe(
                (response: any) => {
                    this.router.navigate(["users"]);
                    this.toastsManager
                        .success("User ".concat(this.userForm.value.username).concat(" has been created successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to save ".concat(this.userForm.value.username), "Failure");
                }
            );
    }

    private updateUser() {
        this.apiService
            .put("/users/".concat(this.id.toString()), this.userForm.value)
            .subscribe(
                (response: any) => {
                    this.router.navigate(["users"]);
                    this.toastsManager
                        .success("User ".concat(this.userForm.value.username).concat(" has been updated successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update ".concat(this.userForm.value.username), "Failure")
                }
            );
    }

    private validateUsername(usernameInput: HTMLInputElement): void {
        let username = usernameInput.value;
        this.additionalUsernameErrors.invalidEmail = !this.emailRegex.test(username);

        this.apiService
            .get("/users/exists", [new QueryParameter("username", username)])
            .subscribe(
                (response: Response) => {
                    let json = response.json();
                    this.additionalUsernameErrors.alreadyExists = this.newUser() && json.exists;
                },
                (error: any) => {
                    this.toastsManager.error("Failed to check whether or not user with specified email address exists", "Failure")
                }
            );
    }
}



