import {Component, OnInit, ElementRef, ViewChild, NgZone, Inject} from "@angular/core";
import {ToastsManager} from "ng2-toastr";
import {FormBuilder, FormGroup, Validators, AbstractControl} from "@angular/forms";
import {ApiService} from "../../services/api.service";
import {ActivatedRoute, Router} from "@angular/router";
import {Response} from "@angular/http";
import {MapsAPILoader} from "angular2-google-maps/core";
import * as moment from "moment";
import {NcfcDatepicker} from "../../common/datepicker/datepicker-directive";

function dateValidator(control: AbstractControl): {[s: string]: boolean} {
    if (!control.parent) {
        return ;
    }

    let registrationDate = control.parent.controls["registrationDate"];
    let startDate = control.parent.controls["startDate"];
    let endDate = control.parent.controls["endDate"];

    if (registrationDate.dirty && startDate.dirty && endDate.dirty) {

        let registeration = moment(+registrationDate.value);
        let start = moment(+startDate.value);
        let end = moment(+endDate.value);

        if (registeration.isAfter(start) || start.isAfter(end)) {
            return {wrongOrder: true};
        }
    }

    if (registrationDate.hasError("wrongOrder")) {
        registrationDate.setErrors(null, {emitEvent: true});
    }

    if (startDate.hasError("wrongOrder")) {
        startDate.setErrors(null, {emitEvent: true});
    }

    if (endDate.hasError("wrongOrder")) {
        endDate.setErrors(null, {emitEvent: true});
    }
}

@Component({
    selector: 'event-form',
    templateUrl: 'event-form.html',
    styles: [`
        .sebm-google-map-container {
            height: 350px;
        }
  `]
})
export class EventFormComponent implements OnInit {
    private pageLoadingComplete: boolean = false;
    private id: number;
    private event: any = {};

    private user: any;

    private allCategories: Array<any>;
    private allOrganizations: Array<any>;
    private allLevels: Array<any>;
    private allRegions: Array<any>;

    private eventForm: FormGroup;

    @ViewChild("location") locationRef: ElementRef;

    @ViewChild("registrationPicker", {read: NcfcDatepicker}) registrationPicker: NcfcDatepicker;
    @ViewChild("startPicker", {read: NcfcDatepicker}) startPicker: NcfcDatepicker;
    @ViewChild("endPicker", {read: NcfcDatepicker}) endPicker: NcfcDatepicker;

    constructor(@Inject("currentUser") private currentUser: string,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager,
                private mapsApiLoader: MapsAPILoader,
                private ngZone: NgZone) {

        this.activatedRoute.params.subscribe((params: any) => this.id = params['id']);

        this.user = JSON.parse(localStorage.getItem(currentUser));
    }

    ngOnInit(): void {
        this.getCategories();
        this.getOrganizations();
        this.getLevels();
        this.getRegions();

        if (this.newEvent()) {
            this.initEvent();
            this.initForm();
        } else {
            this.loadEvent();
        }
    }

    private newEvent() {
        return isNaN(this.id) ? true : false;
    }

    private loadEvent() {
        this.apiService
            .get("/events/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.event = response.json();
                    this.initForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve event with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    formatDates(): void {
        if (!this.event.id) {
            return;
        }

        this.registrationPicker.setDate(this.event.registrationDate);
        this.startPicker.setDate(this.event.startDate);
        this.endPicker.setDate(this.event.endDate);
    }

    private initEvent() {
        this.event = {
            title: undefined,
            description: undefined,
            registrationDate: undefined,
            startDate: undefined,
            endDate: undefined,
            geoDescription: undefined,
            latitude: undefined,
            longitude: undefined,
            mapZoom: undefined,
            organizationId: undefined,
            regionId: undefined,
            levelId: undefined,
            categoryId: undefined,
            createdBy: undefined
        };
    }

    private initForm() {
        this.eventForm = this.formBuilder.group({
            title: [this.event.title, Validators.required],
            description: [this.event.description],
            registrationDate: [this.event.registrationDate, Validators.compose([Validators.required, dateValidator])],
            startDate: [this.event.startDate, Validators.compose([Validators.required, dateValidator])],
            endDate: [this.event.endDate, Validators.compose([Validators.required, dateValidator])],
            geoDescription: [this.event.geoDescription],
            latitude: [this.event.latitude],
            longitude: [this.event.longitude],
            mapZoom: [this.event.mapZoom],
            organizationId: [this.event.organizationId, Validators.required],
            regionId: [this.event.regionId, Validators.required],
            levelId: [this.event.levelId, Validators.required],
            categoryId: [this.event.categoryId, Validators.required],
            createdBy: [this.event.createdBy]
        });

        setTimeout(() => {
            this.initLocationAutocomplete(); /* Timeout is necessary here to init google autocomplete properly  */
            this.formatDates();
        }, 10);

        this.initLocation();
        this.pageLoadingComplete = true;
    }

    private getCategories() {
        this.apiService
            .get("/categories")
            .subscribe(
                (response: Response) => {
                    this.allCategories = response.json()
                        .reduce(
                            function(result, value){
                                if (value.parentId === value.id) {
                                    result[value.id] = Object.assign({}, result[value.id] || {options: []}, value)
                                } else {
                                    (result[value.parentId] || (result[value.parentId] = {options: []})).options.push(value);
                                }
                                return result;
                            },
                            []
                        )
                        .filter(x => !!x);
                },
                (error: any) => {
                    this.toastsManager.error("Failed to get categories list", "Failure")
                }
            );
    }

    private getOrganizations() {
        this.apiService
            .get("/organizations")
            .subscribe(
                (response: Response) => {
                    this.allOrganizations = response.json();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to get organizations list", "Failure")
                }
            );
    }

    private getLevels() {
        this.apiService
            .get("/levels")
            .subscribe(
                (response: Response) => {
                    this.allLevels = response.json();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to get levels list", "Failure")
                }
            );
    }

    private getRegions() {
        this.apiService
            .get("/regions")
            .subscribe(
                (response: Response) => {
                    this.allRegions = response.json();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to get regions list", "Failure")
                }
            );
    }

    private initLocation() {
        if (!this.event.latitude && !this.event.longitude && "geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
                this.event.latitude = position.coords.latitude;
                this.event.longitude = position.coords.longitude;
                this.event.mapZoom = 12;
            });
        }
    }

    private initLocationAutocomplete() {
        this.mapsApiLoader.load().then(() => {
            let autocomplete = new google.maps.places.Autocomplete(this.locationRef.nativeElement, {
                types: ["address"]
            });
            autocomplete.addListener("place_changed", () => {
                this.ngZone.run(() => {
                    let place: google.maps.places.PlaceResult = autocomplete.getPlace();

                    if (place.geometry === undefined || place.geometry === null) {
                        return;
                    }

                    this.eventForm.controls["latitude"].setValue(place.geometry.location.lat());
                    this.eventForm.controls["longitude"].setValue(place.geometry.location.lng());
                    this.eventForm.controls["mapZoom"].setValue(12);

                    this.event.latitude = place.geometry.location.lat();
                    this.event.longitude = place.geometry.location.lng();
                    this.event.mapZoom = 12;
                });
            });
        });
    }

    private onSubmit() {
        this.toMillis("registrationDate");
        this.toMillis("startDate");
        this.toMillis("endDate");

        if (this.newEvent()) {
            this.setCreatedBy();
            this.saveEvent();
        } else {
            this.updateEvent();
        }
    }

    private saveEvent() {
        this.apiService
            .post("/events", this.eventForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["events"]);
                    this.toastsManager.success("Event ".concat(this.eventForm.controls["title"].value).concat(" has been created"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to create event ".concat(this.eventForm.controls["title"].value), "Failure");
                }
            );
    }

    private updateEvent() {
        this.apiService
            .put("/events/".concat(this.event.id), this.eventForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["events"]);
                    this.toastsManager.success("Event ".concat(this.eventForm.controls["title"].value).concat(" has been updated"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update event ".concat(this.eventForm.controls["title"].value), "Failure");
                }
            );
    }

    private toMillis(key: string) {
        let millis = this.eventForm.controls[key].value.length === 10
            ? 1000*+this.eventForm.controls[key].value : this.eventForm.controls[key].value;
        this.eventForm.controls[key].setValue(millis);
    }

    private setCreatedBy() {
        this.eventForm.controls["createdBy"].setValue(this.user.id);
    }
}



