import {Component, OnInit, Input, EventEmitter, Output} from "@angular/core";
import {ApiService} from "../../services/api.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: '[user-item]',
    template:
        `
            <td class="icon-cell">
                <a role="tooltip" aria-haspopup="true" class="tooltip tooltip-xs" [routerLink]="user.id">
                    <clr-icon shape="edit"></clr-icon>
                    <span class="tooltip-content">Edit</span>
                </a>
                <a role="tooltip" aria-haspopup="true" class="tooltip tooltip-xs" (click)="confirm($event)">
                    <clr-icon shape="trash"></clr-icon>
                    <span class="tooltip-content">Delete</span>
                </a>
            </td>
            <td>{{user.id}}</td>
            <td>{{user.username}}</td>
            <td>{{extractUserRoles()}}</td>
            <td>{{toString(user.external)}}</td>
            <td>{{toString(user.enabled)}}</td>
            
            <clr-modal [(clrModalOpen)]="opened">
                <h3 class="modal-title">User removal</h3>
                      
                <div class="modal-body">
                    <p>Do you really want to remove {{user.username}}?</p>
                </div>
                      
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline" (click)="opened = false">Cancel</button>
                    <button type="button" class="btn btn-primary" (click)="deleteUser(user.id)">Ok</button>
                </div>
            </clr-modal>
        `
})
export class UserItemComponent implements OnInit {
    @Input() user: any;
    @Output() onUserRemoval: EventEmitter<any>;

    opened: boolean = false;

    constructor(private apiService: ApiService,
                private toastsManager: ToastsManager) {

        this.onUserRemoval = new EventEmitter();
    }

    ngOnInit() {
        this.extractUserRoles();
    }

    extractUserRoles() {
        let roles: string = "";
        this.user.roles.forEach((role: any) => {
            if (roles.length > 0) roles = roles.concat(", ");
            roles = roles.concat(role.authority.substring(role.authority.indexOf("_") + 1));
        });
        return roles;
    }

    toString(value: boolean) {
        return value ? "Yes" : "No";
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    deleteUser() {
        this.apiService
            .delete("/users/".concat(this.user.id))
            .subscribe(
                (response: Response) => {
                    this.onUserRemoval.emit(this.user);
                    this.toggleDialog();
                    this.toastsManager.success(this.user.username.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    let json = error.json();
                    if (json.message === "Access is denied") {
                        this.toastsManager.warning("You don't have enough permissions to delete users", "Access Denied")
                    } else {
                        this.toastsManager.error("Failed to delete ".concat(this.user.username), "Failure");
                    }
                    this.toggleDialog();
                }
            );
    }

    toggleDialog() {
        this.opened = !this.opened;
    }
}



