import {Component, Inject} from "@angular/core";
import {AuthenticationService} from "../services/authentication.service";
import {Response} from "@angular/http";
import {Router} from "@angular/router";

@Component({
    selector: '[app-header]',
    templateUrl: './header.component.html',
})

export class HeaderComponent {
    private title: string = 'NCF Application';
    private user: any = {};

    constructor(private authenticationService: AuthenticationService, private router: Router, @Inject("currentUser") private currentUser: string) {
        this.user = JSON.parse(localStorage.getItem(this.currentUser));
    }

    logout(event) {
        event.preventDefault();
        this.authenticationService.logout()
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["login"]);
                },
                (error: any) => {
                    console.log("Failed to complete logout request", error);
                }
            );
    }

}



