import {Component, OnInit} from "@angular/core";
import {Response} from "@angular/http";
import {ApiService} from "../../services/api.service";
import {Router, ActivatedRoute} from "@angular/router";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: 'user-list',
    template: `<div spinner [spinnerClasses]="['spinner-lg']" *ngIf="!pageLoadingComplete"></div>
                <div *ngIf="pageLoadingComplete">
                    <p><a [routerLink]="['./add']" class="btn btn-outline">Create User</a></p>
                    
                    <table class="table">
                        <thead>
                            <tr>
                                <th></th>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Roles</th>
                                <th>External</th>
                                <th>Enabled</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr *ngFor="let user of users" user-item [user]="user" (onUserRemoval)="removeFromList($event)"></tr>
                        </tbody>
                    </table>
                </div>`
})
export class UserListComponent implements OnInit {
    private pageLoadingComplete: boolean = false;
    private users: Array<any>;

    constructor(private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {

        this.users = [];
    }

    ngOnInit(): void {
        this.fetchUsers();
    }

    fetchUsers() {
        this.apiService
            .get("/users")
            .subscribe(
                (response: Response) => {
                    this.users = response.json();
                    this.setPageLoaded(true);
                },
                (error: any) => {
                    this.setPageLoaded(true);
                    this.toastsManager.error("Failed to get user list", "Failure");
                }
            );
    }

    private setPageLoaded(completionStatus: boolean): void {
        this.pageLoadingComplete = completionStatus;
    }

    private removeFromList(removedUser: any): void {
        this.users = this.users.filter((user: any) => {
            return removedUser.id !== user.id;
        });
    }

    getUsers(): Array<any> {
        return this.users;
    }
}



