import {Directive, OnInit, ElementRef, Inject, OnDestroy} from "@angular/core";
import {AuthenticationService} from "../../services/authentication.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr";
import {Router} from "@angular/router";
import {Subject, BehaviorSubject} from "rxjs";

@Directive({
    selector: "[activity-tracker]"
})
export class ActivityTracker implements OnInit, OnDestroy {
    private CHECK_INTERVAL_MSEC = 60000;

    private intervalPromise: any;
    private timeToLogout: number;
    private activityObservable: Subject<number>;

    constructor(@Inject("sessionExpirationTime") private expirationTime: number,
                private authenticationService: AuthenticationService,
                private toastManager: ToastsManager,
                private router: Router,
                private hostElement: ElementRef) {}

    ngOnInit(): void {
        this.resetExpirationTime();
        this.activityObservable = new BehaviorSubject(this.expirationTime);

        this.subscribe();
        this.countDown();
        this.trackUserActivity();
    }

    private subscribe(): void {
        this.activityObservable.subscribe((timeToLogout: number) => {
            if (timeToLogout === 0 && this.authenticationService.isLoggedIn()) {
                this.logout();
            }
        });
    };

    private countDown(): void {
        this.intervalPromise = window.setInterval(() => {
            this.activityObservable.next(--this.timeToLogout);
        }, this.CHECK_INTERVAL_MSEC);
    }

    private trackUserActivity(): void {
        this.trackMouseMove();
        this.trackKeyUp();
    }

    private trackMouseMove(): void {
        this.hostElement.nativeElement.onmousemove = (event) => {
            this.resetExpirationTime();
        };
    }

    private trackKeyUp(): void {
        console.log(this.hostElement.nativeElement.onkeyup);
        this.hostElement.nativeElement.onkeyup = (event) => {
            this.resetExpirationTime();
        };
    }

    private resetExpirationTime() {
        this.timeToLogout = this.expirationTime;
    }

    private logout(): void {
        this.authenticationService.logout()
            .subscribe(
                (response: Response) => {
                    this.toastManager.warning("Your session has expired, please login", "Session expired");
                    this.router.navigateByUrl("login");
                },
                (error: any) => {
                    this.toastManager.warning("Unsuccessful logout attempt", "Session expired");
                    this.router.navigateByUrl("login");
                }
            );
    }

    ngOnDestroy(): void {
        window.clearInterval(this.intervalPromise);
    }
}


