import {Component, OnInit, Input, EventEmitter, Output, Inject} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {ApiService} from "../../services/api.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: '[organization-item]',
    templateUrl: './organization-item.component.html'
})
export class OrganizationItemComponent implements OnInit {
    @Input() organization: any;
    @Output() onRemoval: EventEmitter<any>;

    private opened: boolean = false;

    constructor(@Inject("apiUrl") private apiUrl: string,
                private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {

        this.onRemoval = new EventEmitter();
    }

    ngOnInit() {

    }

    editOrganization(event: any, organization: any) {
        event.preventDefault();
        this.router.navigate(['./', organization.id], {relativeTo: this.activatedRoute});
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    toggleDialog() {
        this.opened = !this.opened;
    }

    getLogoUrl(): string {
        return this.apiUrl
            .concat("/attachments/")
            .concat(this.organization.attachmentId)
            .concat("?size=50");
    }

    deleteOrganization(id: number) {
        this.apiService
            .delete("/organizations/".concat(this.organization.id))
            .subscribe(
                (response: Response) => {
                    this.onRemoval.emit(this.organization);
                    this.toggleDialog();
                    this.toastsManager.success(this.organization.name.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to delete ".concat(this.organization.name), "Failure");
                }
            );
    }
}



