import { Component, OnInit } from '@angular/core';
import {Category} from "../category";
import {ActivatedRoute, Router} from "@angular/router";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {Response} from "@angular/http";

@Component({
  selector: 'app-category-form',
  templateUrl: './category-form.component.html',
  styles: []
})
export class CategoryFormComponent implements OnInit {
    private id: number;
    private category: any;
    private allCategories: Array<any>;
    private parentCategories: Array<any>;
    private listOfThemes: Array<any>;
    private categoryForm: FormGroup;
    private pageLoadingComplete: boolean = false;

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager) {

        this.activatedRoute.params.subscribe((params: any) => this.id = params['id']);

        this.category = {
            name: undefined,
            theme: undefined,
            parentId: undefined
        };
    }

    ngOnInit() {
        this.getCategoriesData();
        if (this.newCategory()) {
            this.initForm();
        } else {
            this.loadCategory();
        }
    }

    loadCategory() {
        this.apiService
            .get("/categories/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.category = response.json();
                    this.initForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve user with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    private getCategoriesData() {
        this.apiService
            .get("/categories")
            .subscribe(
                (response: Response) => {
                    this.allCategories = response.json();
                    this.parentCategories = this.allCategories.filter(function(value) {
                        return value.id === value.parentId;
                    });
                    this.listOfThemes = this.allCategories.map(function(value) {
                        return value.theme;
                    });
                    this.listOfThemes = this.listOfThemes.filter(function(value) {
                        return value !== null;
                    });
                },
                (error: any) => {
                    this.toastsManager.error("Failed to get categories list", "Failure")
                }
            );
    }


    private initForm() {
        this.categoryForm = this.formBuilder.group({
            name: [this.category.name, Validators.required],
            theme: [this.category.theme],
            parentId: [this.category.parentId]

        });
        this.pageLoadingComplete = true;
    }

    private newCategory() {
        return isNaN(this.id) ? true : false;
    }


    onSubmit() {
        if (this.newCategory()) {
            this.saveCategory();
        } else {
            this.updateCategory();
        }
    }

    private saveCategory(): void {
        this.apiService
            .post("/categories", this.categoryForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["categories"]);
                    this.toastsManager
                        .success("Category ".concat(this.categoryForm.value.name).concat(" has been created successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to save ".concat(this.categoryForm.value.name), "Failure")
                }
            );
    }


    private updateCategory(): void {
        console.log(this.categoryForm.value);
        this.apiService
            .put("/categories/".concat(this.id.toString()), this.categoryForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["categories"]);
                    this.toastsManager
                        .success("Level ".concat(this.categoryForm.value.name).concat(" has been updated successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update ".concat(this.categoryForm.value.name), "Failure")
                }
            );
    }


}



