import {Directive, Input, ElementRef, OnInit} from "@angular/core";

declare function require(path: string): any;
let Flatpickr = require("flatpickr");

/**
 *  Wrapper directive for flatpickr.
 *  For exhausting reference see https://chmln.github.io/flatpickr/
 *
 */
@Directive({
    selector: '[datepicker]'
})
export class NcfcDatepicker implements OnInit {
    @Input() config: any;

    private datepicker: any;
    private defaultConfig: any;

    constructor(private hostElement: ElementRef) {
        this.defaultConfig = {
            dateFormat: 'U',
            altInput: true,
            altFormat: 'd-F-Y H:i K',
            enableTime: true,
            weekNumbers: true
        };
    }

    ngOnInit(): void {
        this.datepicker = new Flatpickr(this.hostElement.nativeElement, this.config ? this.config :this.defaultConfig);
    }

    public setDate(value: number): void {
        this.datepicker.setDate(value);
    }
}


