import {Routes, RouterModule} from "@angular/router";
import {LoginComponent} from "./login/login.component";
import {CategoriesComponent} from "./categories/categories.component";
import {OrganizationsComponent} from "./organizations/organizations.component";
import {UsersComponent} from "./users/users.component";
import {LevelsComponent} from "./levels/levels.component";
import {RegionsComponent} from "./regions/regions.component";
import {EventsComponent} from "./events/events.component";
import {ORGANIZATION_ROUTES} from "./organizations/organization.routes";
import {CATEGORY_ROUTES} from "./categories/category.routes";
import {REGION_ROUTES} from "./regions/region.routes";
import {LEVEL_ROUTES} from "./levels/level.routes";
import {USER_ROUTES} from "./users/user.routes";
import {EVENT_ROUTES} from "./events/event.routes";
import {RouteProtector} from "./app.protected.route";

const applicationRoutes: Routes = [
    {path: '', redirectTo: 'login', pathMatch: 'full'},
    {path: 'login', component: LoginComponent},
    {
        path: 'categories',
        component: CategoriesComponent,
        children: CATEGORY_ROUTES,
        canActivate: [RouteProtector]
    },
    {
        path: 'organizations',
        component: OrganizationsComponent,
        children: ORGANIZATION_ROUTES,
        canActivate: [RouteProtector]
    },
    {
        path: 'levels',
        component: LevelsComponent,
        children: LEVEL_ROUTES,
        canActivate: [RouteProtector]
    },
    {
        path: 'regions',
        component: RegionsComponent,
        children: REGION_ROUTES,
        canActivate: [RouteProtector]
    },
    {
        path: 'users',
        component: UsersComponent,
        children: USER_ROUTES,
        canActivate: [RouteProtector]
    },
    {
        path: 'events',
        component: EventsComponent,
        children: EVENT_ROUTES,
        canActivate: [RouteProtector]
    }
];

export const routing = RouterModule.forRoot(applicationRoutes);



