import {Component} from "@angular/core";

@Component({
    selector: 'users',
    template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class UsersComponent {
    private pageHeader: string;

    constructor() {
        this.pageHeader = "Users";
    }
}



