import {Component} from "@angular/core";

@Component({
  selector: 'app-levels',
  template: `<h3>{{pageHeader}}</h3><router-outlet></router-outlet>`
})
export class LevelsComponent {
  private pageHeader: string;

  constructor() {
    this.pageHeader = "Levels";
  }
}



