import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {AttachmentService} from "../../services/attachment.service";
import {Response} from "@angular/http";

@Component({
  selector: 'organization-form',
  templateUrl: './organization-form.component.html'
})

export class OrganizationFormComponent implements OnInit {
    private id: number;
    private attachmentId: number;
    private organization: any;
    private organizationForm: FormGroup;
    private pageLoadingComplete: boolean = false;

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private attachmentService: AttachmentService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager) {

        this.activatedRoute.params.subscribe((params: any) => this.id = params['id']);

        this.organization = {
            name: undefined,
            description: undefined,
            attachmentId: undefined
        };
    }

    ngOnInit(): void {
        if (this.newOrganization()) {
            this.initForm();
        } else {
            this.loadOrganization();
        }
    }

    loadOrganization() {
        this.apiService
            .get("/organizations/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.organization = response.json();
                    this.attachmentId = this.organization.attachmentId;
                    this.initForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve user with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    private initForm() {
        this.organizationForm = this.formBuilder.group({
            name: [this.organization.name, Validators.required],
            description: [this.organization.description],
            attachmentId: [this.organization.attachmentId]
        });
        this.pageLoadingComplete = true;
    }

    private newOrganization(): boolean {
        return isNaN(this.id) ? true : false;
    }

    private handleSelection(fileInput) {
        if (!fileInput.files) {
            return;
        }
        let selectedFile: File = fileInput.files[0];
        this.attachmentService
            .upload(selectedFile)
            .subscribe(
                (response: Response) => {
                    this.toastsManager.success("Attachment has been uploaded ");
                    if (this.attachmentId) {
                        this.removePreviouslyUploaded();
                    }
                    this.attachmentId = response.json();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to upload attachment ", selectedFile.name);
                }
            );
    }

    private removePreviouslyUploaded() {
        this.apiService
            .delete("/attachments/".concat(this.attachmentId.toString()))
            .subscribe(
                (response: Response) => {},
                (error: any) => {
                    this.toastsManager.warning("Failed to remove previously uploaded file", "Unused upload removal");
                }
            );
    }

    private getAttachmentUrl() {
        return this.attachmentService.getAttachmentUrl(this.attachmentId);
    }

    private onSubmit(): void {
        this.setAttachment();
        if (this.newOrganization()) {
            this.saveOrganization();
        } else {
            this.updateOrganization();
        }
    }

    private saveOrganization(): void {
        this.apiService
            .post("/organizations", this.organizationForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["organizations"]);
                    this.toastsManager
                        .success("Organization ".concat(this.organizationForm.value.name).concat(" has been created successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to save ".concat(this.organizationForm.value.name), "Failure")
                }
            );
    }

    private updateOrganization(): void {
        this.apiService
            .put("/organizations/".concat(this.id.toString()), this.organizationForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["organizations"]);
                    this.toastsManager
                        .success("Organization ".concat(this.organizationForm.value.name).concat(" has been updated successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update ".concat(this.organizationForm.value.name), "Failure")
                }
            );
    }

    private setAttachment(): void {
        this.organizationForm.controls["attachmentId"].setValue(this.attachmentId);
    }
}




