import {BrowserModule} from "@angular/platform-browser";
import {NgModule} from "@angular/core";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {HttpModule, Http, BrowserXhr} from "@angular/http";
import {LocationStrategy, HashLocationStrategy} from "@angular/common";
import {ClarityModule} from "clarity-angular";
import {AppComponent} from "./app.component";
import {LoginComponent} from "./login/login.component";
import {HeaderComponent} from "./header/header.component";
import {OrganizationListComponent} from "./organizations/organization-list/organization-list.component";
import {SidebarComponent} from "./sidebar/sidebar.component";
import {routing} from "./app.routing";
import {OrganizationFormComponent} from "./organizations/organization-form/organization-form.component";
import {CategoriesComponent} from "./categories/categories.component";
import {CategoryListComponent} from "./categories/category-list/category-list.component";
import {CategoryFormComponent} from "./categories/category-form/category-form.component";
import {UserFormComponent} from "./users/user-form/user-form.component";
import {UsersComponent} from "./users/users.component";
import {UserListComponent} from "./users/user-list/user-list.component";
import {UserItemComponent} from "./users/user-list/user-item.component";
import {environment} from "../environments/environment";
import {LevelsComponent} from "./levels/levels.component";
import {RegionListComponent} from "./regions/region-list/region-list.component";
import {RegionFormComponent} from "./regions/region-form/region-form.component";
import {OrganizationsComponent} from "./organizations/organizations.component";
import {LevelListComponent} from "./levels/level-list/level-list.component";
import {LevelFormComponent} from "./levels/level-form/level-form.component";
import {RegionsComponent} from "./regions/regions.component";
import {AuthenticationService} from "./services/authentication.service";
import {ApiService} from "./services/api.service";
import {RouteProtector} from "./app.protected.route";
import {CorsService} from "./services/cors.service";
import {SpinnerComponent} from "./common/spinner/spinner.component";
import {ToastModule, ToastOptions} from "ng2-toastr/ng2-toastr";
import {OrganizationItemComponent} from "./organizations/organization-item/organization-item.component";
import {AttachmentService} from "./services/attachment.service";
import {EventsComponent} from "./events/events.component";
import {EventListComponent} from "./events/event-list/event-list.component";
import {EventFormComponent} from "./events/event-form/event-form.component";
import {EventItemComponent} from "./events/event-item/event-item.component";
import {Trimmer} from "./common/pipes/trimmer-pipe";
import {AgmCoreModule} from "angular2-google-maps/core";
import {NcfcDatepicker} from "./common/datepicker/datepicker-directive";
import {NcfcPagerComponent} from "./common/pager/pager.component";
import { CategoryItemComponent } from './categories/category-item/category-item.component';
import { LevelItemComponent } from './levels/level-item/level-item.component';
import { RegionItemComponent } from './regions/region-item/region-item.component';
import {ActivityTracker} from "./common/activity-tracker/activity.tracker.directive";

export function apiServiceFactory(apiUrl: string, http: Http) {
    return new ApiService(apiUrl, http);
}

export function attachmentServiceFactory(apiUrl: string, http: Http) {
    return new AttachmentService(apiUrl, http);
}

export function authenticationServiceFactory(apiService: ApiService, currentUser: string) {
    return new AuthenticationService(apiService, currentUser);
}

export function toastsOptions() {
    return new ToastOptions({
        showCloseButton: true,
        animate: "fade"
    });
}

@NgModule({
    declarations: [
        AppComponent,
        UsersComponent,
        OrganizationsComponent,
        CategoriesComponent,
        LoginComponent,
        HeaderComponent,
        SidebarComponent,
        CategoriesComponent,
        CategoryListComponent,
        OrganizationsComponent,
        OrganizationListComponent,
        OrganizationItemComponent,
        SidebarComponent,
        OrganizationFormComponent,
        CategoryFormComponent,
        UserFormComponent,
        UserListComponent,
        UserItemComponent,
        EventsComponent,
        EventListComponent,
        EventItemComponent,
        EventFormComponent,
        LevelsComponent,
        LevelListComponent,
        LevelFormComponent,
        RegionsComponent,
        RegionListComponent,
        RegionFormComponent,
        SpinnerComponent,
        Trimmer,
        NcfcDatepicker,
        NcfcPagerComponent,
        CategoryItemComponent,
        LevelItemComponent,
        RegionItemComponent,
        ActivityTracker
    ],
    imports: [
        BrowserModule,
        ToastModule.forRoot(ToastOptions),
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        ClarityModule.forChild(),
        routing,
        AgmCoreModule.forRoot({
            apiKey: "AIzaSyA1vysDtyLk9sZz-QL0B26zmDF4rTlpG1Q",
            libraries: ["places"]
        })
    ],
    providers: [
        {provide: LocationStrategy, useClass: HashLocationStrategy},
        {provide: BrowserXhr, useClass: CorsService},
        {provide: 'apiUrl', useValue: environment.apiUrl},
        {provide: 'currentUser', useValue: environment.currentUser},
        {provide: 'sessionExpirationTime', useValue: environment.sessionExpirationTimeSeconds},
        {
            provide: ApiService,
            useFactory: apiServiceFactory,
            deps: ['apiUrl', Http]
        },
        {
            provide: AttachmentService,
            useFactory: attachmentServiceFactory,
            deps: ['apiUrl', Http]
        },
        {
            provide: AuthenticationService,
            useFactory: authenticationServiceFactory,
            deps: [ApiService, 'currentUser']
        },
        {provide: RouteProtector, useClass: RouteProtector},
        {
            provide: ToastOptions,
            useFactory: toastsOptions
        }
    ],
    bootstrap: [AppComponent]
})
export class AppModule {
}



