import {Component, OnInit} from '@angular/core';
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {Response} from "@angular/http";

@Component({
    selector: 'categories',
    templateUrl: './category-list.component.html'
})
export class CategoryListComponent implements OnInit {
    private pageLoadingComplete: boolean = false;
    private categories: Array<any>;

    constructor(private apiService: ApiService,
                private toastsManager: ToastsManager) {
    }

    ngOnInit() {
        this.fetchCategories();
    }

    fetchCategories() {
        this.apiService
            .get("/categories")
            .subscribe(
                (response: Response) => {
                    this.categories = response.json();
                    this.setPageLoaded(true);
                },
                (error: any) => {
                    this.setPageLoaded(true);
                    this.toastsManager.error("Failed to get category list", "Failure");
                }
            );
    }

    private setPageLoaded(completionStatus: boolean): void {
        this.pageLoadingComplete = completionStatus;
    }

    removeFromList(removedCategory: any): void {
        this.categories = this.categories.filter((category: any) => {
            return removedCategory.id !== category.id;
        });
    }

}



