import {Component, Input, EventEmitter, Output, Inject} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {ApiService} from "../../services/api.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
  selector: '[level-item]',
  templateUrl: './level-item.component.html',
})
export class LevelItemComponent {

    @Input() level: any;
    @Output() onRemoval: EventEmitter<any>;

    private opened: boolean = false;

    constructor(@Inject("apiUrl") private apiUrl: string,
                private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {

        this.onRemoval = new EventEmitter();
    }

    editLevel(event: any, level: any) {
        event.preventDefault();
        this.router.navigate(['./', level.id], {relativeTo: this.activatedRoute});
    }

    confirm(event) {
        event.preventDefault();
        this.toggleDialog();
    }

    toggleDialog() {
        this.opened = !this.opened;
    }

    deleteLevel() {
        this.apiService
            .delete("/levels/".concat(this.level.id))
            .subscribe(
                (response: Response) => {
                    this.onRemoval.emit(this.level);
                    this.toggleDialog();
                    this.toastsManager.success(this.level.name.concat(" has been deleted"), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to delete ".concat(this.level.name), "Failure");
                }
            );
    }

}



