import {Component, OnInit} from "@angular/core";
import {ActivatedRoute, Router} from "@angular/router";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {Response} from "@angular/http";

@Component({
    selector: 'organizations',
    templateUrl: './organization-list.component.html'
})
export class OrganizationListComponent implements OnInit {
    private pageLoadingComplete: boolean = false;
    private organizations: Array<any>;

    constructor(private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {
    }

    ngOnInit() {
        this.fetchOrganizations();
    }

    fetchOrganizations() {
        this.apiService
            .get("/organizations")
            .subscribe(
                (response: Response) => {
                    this.organizations = response.json();
                    this.setPageLoaded(true);
                },
                (error: any) => {
                    this.setPageLoaded(true);
                    this.toastsManager.error("Failed to get organization list", "Failure");
                }
            );
    }

    private setPageLoaded(completionStatus: boolean): void {
        this.pageLoadingComplete = completionStatus;
    }

    createOrganization() {
        this.router.navigate(['./', "add"], {relativeTo: this.activatedRoute});
    }

    removeFromList(removedOrganization: any): void {
        this.organizations = this.organizations.filter((organization: any) => {
            return removedOrganization.id !== organization.id;
        });
    }
}



