import { Component, OnInit } from '@angular/core';
import {Level} from "../level";
import {ActivatedRoute, Router} from "@angular/router";
import {FormGroup, FormBuilder, Validators} from "@angular/forms";
import {ApiService} from "../../services/api.service";
import {ToastsManager} from "ng2-toastr/ng2-toastr";
import {Response} from "@angular/http";

@Component({
    selector: 'app-level-form',
    templateUrl: './level-form.component.html',
})
export class LevelFormComponent implements OnInit {
    private id: number;
    private level: any;
    private levelForm: FormGroup;
    private pageLoadingComplete: boolean = false;

    constructor(private router: Router,
                private activatedRoute: ActivatedRoute,
                private apiService: ApiService,
                private formBuilder: FormBuilder,
                private toastsManager: ToastsManager) {

        this.activatedRoute.params.subscribe((params: any) => this.id = params['id']);

        this.level = {
            name: undefined,
            deleted: [false],
        };
    }

    ngOnInit() {
        if (this.newLevel()) {
            this.initForm();
        } else {
            this.loadLevel();
        }
    }

    loadLevel() {
        this.apiService
            .get("/levels/".concat(this.id.toString()))
            .subscribe(
                (response: Response) => {
                    this.level = response.json();
                    this.initForm();
                },
                (error: any) => {
                    this.toastsManager.error("Failed to retrieve user with id ".concat(this.id.toString()), "Failure");
                }
            );
    }

    private initForm() {
        this.levelForm = this.formBuilder.group({
            name: [this.level.name, Validators.required]
        });
        this.pageLoadingComplete = true;
    }

    private newLevel() {
        return isNaN(this.id) ? true : false;
    }

    onSubmit() {
        if (this.newLevel()) {
            this.saveLevel();
        } else {
            this.updateLevel();
        }
    }

    private saveLevel(): void {
        this.apiService
            .post("/levels", this.levelForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["levels"]);
                    this.toastsManager
                        .success("Level ".concat(this.levelForm.value.name).concat(" has been created successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to save ".concat(this.levelForm.value.name), "Failure")
                }
            );
    }

    private updateLevel(): void {
        this.apiService
            .put("/levels/".concat(this.id.toString()), this.levelForm.value)
            .subscribe(
                (response: Response) => {
                    this.router.navigate(["levels"]);
                    this.toastsManager
                        .success("Level ".concat(this.levelForm.value.name).concat(" has been updated successfully."), "Success");
                },
                (error: any) => {
                    this.toastsManager.error("Failed to update ".concat(this.levelForm.value.name), "Failure")
                }
            );
    }


}



