import {Component} from "@angular/core";
import {ApiService} from "../../services/api.service";
import {Router, ActivatedRoute} from "@angular/router";
import {ToastsManager} from "ng2-toastr";

@Component({
    selector: 'event-list',
    templateUrl: './event-list.component.html'
})
export class EventListComponent {
    private pageLoadingComplete: boolean = false;
    private events: Array<any>;

    constructor(private apiService: ApiService,
                private router: Router,
                private activatedRoute: ActivatedRoute,
                private toastsManager: ToastsManager) {
    }

    private setPageLoaded(completionStatus: boolean): void {
        this.pageLoadingComplete = completionStatus;
    }

    createEvent() {
        this.router.navigate(['./', "add"], {relativeTo: this.activatedRoute});
    }

    removeFromList(removedEvent: any): void {
        this.events.forEach((item: any) => {
            if (item.event.id === removedEvent.id) {
                item.event.deleted = true;
            }
        });
    }

    updateEventList(events: Array<any>) {
        this.events = events;
        this.setPageLoaded(true);
    }
}



