import {Component, OnInit, Inject, ViewEncapsulation, ViewContainerRef} from "@angular/core";
import {AuthenticationService} from "./services/authentication.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr/ng2-toastr";

@Component({
    selector: 'ncfc',
    templateUrl: './app.component.html'
})
export class AppComponent implements OnInit {
    constructor(private authenticationService: AuthenticationService,
                @Inject("currentUser") private currentUser: string,
                private toastsManager: ToastsManager,
                containerRef: ViewContainerRef) {

        this.toastsManager.setRootViewContainerRef(containerRef);
    }

    ngOnInit(): void {
        this.performLoginCheck();
    }

    private performLoginCheck(): void {
        if (this.authenticationService.isLoggedIn()) {
            return;
        }

        this.authenticationService.getCurrentUser()
            .subscribe(
                (response: Response) => {
                    localStorage.setItem(this.currentUser, JSON.stringify(response.json()));
                },
                (error: any) => {
                    localStorage.removeItem(this.currentUser);
                    let json = error.json();
                    if (json.message === "USER_IS_NOT_AUTHORIZED") {
                        this.toastsManager.warning("Please sign in to proceed.", "Authorization check")
                    } else {
                        this.toastsManager.error("Failed to get current user.", "Error")
                    }
                }
            );
    }

    isLoggedIn(): boolean {
        return this.authenticationService.isLoggedIn();
    }
}



