import {Component, Input, Output, EventEmitter, OnInit} from "@angular/core";
import {ApiService, QueryParameter} from "../../services/api.service";
import {Response} from "@angular/http";
import {ToastsManager} from "ng2-toastr";

@Component({
    selector: "pager",
    styleUrls: ['pager.css'],
    template:
        `
            <ul class="list-unstyled">
                <li class="pager-link"><button type="button" class="btn btn-outline" (click)="toFirstPage($event)" [disabled]="isFirst()">First</button></li>
                <li class="pager-link"><button class="btn btn-outline" (click)="previousPage($event)" [disabled]="isFirst()">Prev</button></li>
                <li class="pager-link"><input type="text" size="5" class="pager-input" (blur)="toPage(pageInput.value)" value="{{currentPage}}" #pageInput/></li>
                <li class="pager-link"><button class="btn btn-outline" (click)="nextPage($event)" [disabled]="isLast()">Next</button></li>
                <li class="pager-link"><button class="btn btn-outline" (click)="toLastPage($event)" [disabled]="isLast()">Last</button></li>
            </ul>
            
            <div class="page-select-wrapper">
                <label>Display By:</label>
                <div class="select pager-select">
                    <select class="pager-select" (change)="setLimit($event.target.value)">
                        <option value="10">10</option>
                        <option value="15">15</option>
                        <option value="20">20</option>
                        <option value="25">25</option>
                    </select>
                </div>
            </div>
        `
})
export class NcfcPagerComponent implements OnInit {
    @Input() getUrl: string;
    @Output() onPageSelect: EventEmitter<any>;

    private currentPage: number;
    private lastPage: number;
    private limit: number;

    constructor(private apiService: ApiService, private toastManager: ToastsManager) {
        this.onPageSelect = new EventEmitter();
        this.currentPage = 1;
        this.limit = 10;
    }

    ngOnInit(): void {
        this.getTotalPageCount();
    }

    toFirstPage(event): void {
        event.preventDefault();
        this.currentPage = 1;
        this.fetchPage();
    }

    toLastPage(event): void {
        event.preventDefault();
        this.currentPage = this.lastPage;
        this.fetchPage();
    }

    toPage(pageNumber: string) {
        this.currentPage = +pageNumber;
        this.fetchPage();
    }

    nextPage(event): void {
        event.preventDefault();
        ++this.currentPage;
        this.fetchPage();
    }

    previousPage(event): void {
        event.preventDefault();
        --this.currentPage;
        this.fetchPage();
    }

    private fetchPage(): void {
        this.apiService
            .get(this.getUrl, this.appendQueryParameters())
            .subscribe(
                (response: Response) => {
                    this.onPageSelect.emit(response.json());
                },
                (error: any) => {
                    this.toastManager.error("Failed to get event page ".concat(this.currentPage.toString()), "Failure");
                }
            );
    }

    private appendQueryParameters(): Array<QueryParameter> {
        let parameters = [];
        parameters.push({key: "page", value: this.currentPage});
        parameters.push({key: "limit", value: this.limit});
        return parameters;
    }

    private getTotalPageCount(): void {
        this.apiService
            .get("/events/count")
            .subscribe(
                (response: Response) => {
                    let eventsCount: number = response.json().count;
                    this.lastPage = parseInt((eventsCount / this.limit).toString()) + 1;

                    this.toFirstPage(event);
                },
                (error: any) => {
                    this.toastManager.error("Failed to get events count", "Failure");
                }
            );
    }

    isFirst(): boolean {
        return this.currentPage === 1;
    }

    isLast(): boolean {
        return this.currentPage === this.lastPage;
    }

    setLimit(limit): void {
        this.limit = +limit;
        this.getTotalPageCount();
    }
}


